.c-checkbox-poor__label {
  position: relative;

  padding-left: 25px;
  line-height: 18px;
  color: $primary-color-50;
}

.c-checkbox-poor__checkbox_custom {
  position: absolute;
  top: 0;
  left: 0;

  box-sizing: border-box;
  width: 20px;
  height: 20px;

  border: 1px solid $primary-color-25;
  border-radius: 4px;
}

.c-checkbox-poor__checkbox:checked + .c-checkbox-poor__checkbox_custom::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;

  width: 10px;
  height: 11px;

  transform: translate(-50%, -50%);
  background: url('../../components/checkboxes/images/checkbox-arrow.svg') no-repeat center;
}

.c-checkbox-poor__checkbox:focus + .c-checkbox-poor__checkbox_custom {
  outline: 5px auto rgb(77, 144, 254);
  outline-offset: -2px;
}
